.header {
  height: 90px;
  background: #fff;
  color: #000000;
  font-size: 22px;
  font-weight: 400;
  &__row {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
  }
  &__logo {
    margin-right: auto;
  }
  &__nav {
    display: flex;
    justify-content: space-around;
    width: 60%;
  }
  &__right-block {
    margin-left: auto;
    display: flex;
    align-items: center;
  }
  &__call {
    width: 49px;
    height: 49px;
    background-color: $cyprus;
    border-radius: 50%;
    fill: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  &__hamburger {
    display: block;
    margin-left: 38px;
    cursor: pointer;
  }
  &__nav-item {
    cursor: pointer;
    color: inherit;
    display: flex;
    align-items: center;
    &:hover {
      color: inherit;
      text-decoration: none;
    }
  }
}