.promo {
  background: url("/img/raster/circle-lg.png") calc(-600px + 50%) -80% no-repeat,
  url("/img/raster/triangle-lg.png") calc(-260px + 50%) 130% no-repeat,
  url("/img/raster/square-lg.png") calc(880px + 50%) 30% no-repeat #f4f5f5;
  padding: 55px 0;

  &__block {
    background: url("/img/raster/square-md.png") 120% 70% no-repeat,
    url("/img/raster/triangle-md.png") 80% 10% no-repeat,
    url("/img/raster/circle-md.png") -15% -50% no-repeat #fff;
    box-shadow: 0 10px 36px rgba(0, 0, 0, 0.07);
    max-width: 90%;
    margin-left: auto;
    margin-right: auto;
    padding-top: 145px;
    padding-bottom: 115px;
    @media screen and (max-width: 992px) {
      max-width: 98%;
      padding-top: 110px;
      padding-bottom: 70px;
    }
    @media screen and (max-width: 576px) {
      .title-1 {
        font-size: 50px;
      }
    }
  }

  &__title {
    margin-bottom: 78px;
    text-align: center;
  }
}